import { MxGraphNS, mxPoint } from "@edp-pmf/mxgraph-ts";

/**
 * mxgraph工具类
 */
export class MxGraphHelper {
    /**
     * 根据绝对坐标获取相对坐标
     * @param pt 
     * @param scale 
     * @param tr 
     * @returns 
     */
    public static getRelativePositon(pt: MxGraphNS.mxPoint, scale: number, tr: MxGraphNS.mxPoint) {
        return new mxPoint(pt.x / scale - tr.x, pt.y / scale - tr.y);
    }
}